.footer-wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
    flex-wrap: wrap;
    background-color: #d3d3d3;
    .copyright{
        height: 45px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        background-color: #3e3e3e;
        color: #d3d3d3;
        font-weight: bold;
    }
}

.footer{
    font: normal 15px "Roboto";
    color: #3e3e3e;
    width: 1100px;
    .footer-main{
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        flex-wrap: wrap;
        margin: 100px 0;
        &-column{
            flex-grow: 0;
            flex-basis: 25%;
            &-title{
                font-size: bold 20px "Arvo";
                line-height: 20px;
            }
            &-links{
                display: flex;
                justify-content: space-around;
                flex-direction: column;       
                a{
                    color: #3e3e3e;
                }
                .blog-link-title{
                    font-weight: bold;
                }
            }
            form{
                display: inline;
                width: 255px;
                height: 54px;
                input{
                    width: 160px;
                    background-color: #eceaea;
                    border-radius: 10px 0 0 10px;
                    border: none;
                    outline: none;
                }
                button{
                    width: 95px;
                    padding: 6px 15px 6px 15px;
                    background-color: #3e3e3e;
                    border-radius: 0 10px 10px 0;
                    border: none;
                    margin-left: -5px;
                    color: #d3d3d3;
                    font-style: "Arvo";
                    outline: none;
                    &:hover{
                        background-color: #d3d3d3;
                        color: #3e3e3e;

                    }
                }
            }
        }
    }
}